@media all and (min-width:1201px){
  .NavbarContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 25px 10px 25px;
    font-size: 22px;
    font-weight: 500;
    height: auto;
    width: 100vw;
    box-sizing: border-box;
    position: fixed;
    background-color: rgb(0, 0, 0);
    z-index: 2;
  }
    .App {
      margin: 0px;
      padding: 0px;
      box-sizing: border-box;
      text-align: center;
      background-color: black;
      width: 100vw;
      height: 100vh;
      z-index: -1;
      overflow-x: hidden;
    }
    .MenuBar{
      display:none;
    }
    .Link{
      color:rgb(160, 160, 160);
      text-decoration: none;
      cursor: pointer;
      transition: 0.3s;
      padding: 2px 8px 5px 8px;
      border-radius: 6px;
    }
    .colorLink{
      color:rgb(248, 191, 4);
      text-decoration: none;
      cursor: pointer;
      padding: 2px 8px 5px 8px;
      transition: 0.3s;
      border-radius: 6px;
    }
    .Link:hover{
      color: white;
    }
    .Logo{
      width: 50px;
      height: 50px;
      margin-left: 30px;
    }
    .NavbarOptions{
      color: white;
      display: flex;
      justify-content: space-between;
      gap:55px;
      margin-right: 10px;
    }
}

@media (max-width:1200px) and (min-width:901px){
