      text-align: center;
      background-color: black;
      width: 100vw;
      height: 100%;
      z-index: -1;
      box-sizing: border-box;
    }
    .MenuBar{
      display:none;
    }
    .Link{
      color:rgb(160, 160, 160);
      text-decoration: none;
      cursor: pointer;
      transition: 0.3s;
      padding: 2px 8px 5px 8px;
      border-radius: 6px;
      box-sizing: border-box;
    }
    .colorLink{
      color:rgb(248, 191, 4);
      text-decoration: none;
      cursor: pointer;
      padding: 2px 8px 5px 8px;
      transition: 0.3s;
      border-radius: 6px;
      box-sizing: border-box;
    }
    .Link:hover{
      color: white;
    }
    .NavbarContainer{
      display: flex;
      justify-content: space-between;
      padding: 17px 25px 0px;
      font-size: 22px;
      font-weight: 500;
      height: auto;
      width: 100vw;
      box-sizing: border-box;
      position: fixed;
      background-color: rgb(0, 0, 0);
      z-index: 5;
    }
    .Logo{
      width: 45px;
      height: 45px;
      margin-left: 30px;
    }
    .NavbarOptions{
      color: white;
      display: flex;
      justify-content: space-between;
      gap:35px;
    }
}

@media (max-width:900px) and (min-width:601px){
  .MenuBar{
    position: fixed;
    right:30px;
    top:25px;
    font-size: 20px;
    color:white;
    cursor: pointer;
    z-index: 2;
    box-sizing: border-box;
  }
  .Logo{
    width: 40px;
    height: 40px;
  }
  .LogoDiv{
    position: fixed;
    top:15px;
    left:20px
  }
  .NavbarOptions{
    display: none;
  }
  .NavbarOptionsAfter{
    position: fixed;
    top:0px;
    right: 0px;
    width: 250px;
    color: white;
    display: flex;
    background-color: black;
    flex-direction: column;
    justify-content: space-evenly;
    height: 50vh;
    z-index: 1;
    box-sizing: border-box;
    animation-name: MenuBar;
    animation-duration: 1s;
    animation-timing-function: "ease-in-out";
  }
  @keyframes MenuBar {
    0%{
      position: absolute;
      top: 0px;
      right: -200px;
    }
  }
  .Link{
    color:rgb(160, 160, 160);
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;
    padding: 5px 8px 8px 8px;
    font-size: 17px;
    box-sizing: border-box;
  }
  .colorLink{
    color:rgb(248, 191, 4);
    text-decoration: none;
    cursor: pointer;
    padding: 5px 8px 8px 8px;
    transition: 0.3s;
    border-radius: 6px;
    font-size: 20px;
    box-sizing: border-box;
  }
  .Link:hover{
    color: white;
    background-color: rgb(19, 19, 19);
  }
  .NavbarContainer{
    display: flex;
    justify-content: space-between;
    padding: 0px;
    font-size: 22px;
    font-weight: 500;
    width: 100vw;
    box-sizing: border-box;
    height: 60px;
    background-color: black;
    position: fixed;
    top:0px;
    left:0px;
    box-sizing: border-box;
    z-index: 1;
  }
  .App{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    background-color: black;
    width: 100vw;
    height: 100%;
    z-index: -1;
  }
  .HomeContainer{
      width: 100vw;
      display: flex;
      flex-direction: column-reverse;
      justify-content: space-around;
      align-items: center;
      gap:30px;
      height: auto;
      line-height: 1;
      color:white;
      margin: auto;
      padding: 20px;
      box-sizing: border-box;
  }
}



